'use client'

import { useState } from 'react'
import { useAuth } from '../context/AuthContext'
import { QRCodeCanvas } from 'qrcode.react'
import Map from '../components/Map'
import EventManager from '../components/EventManager'
import ClubManager from '../components/ClubManager'
import FriendSystem from '../components/FriendSystem'
import AdminDashboard from '../components/AdminDashboard'
import Monetization from '../components/Monetization'
import Navigation from '../components/Navigation'
import LayoutContent from '../components/LayoutContent'

export default function Home() {
  const { user, login, logout, updateProfile } = useAuth()
  const [email, setEmail] = useState('')
  const [activeTab, setActiveTab] = useState('home')
  const [username, setUsername] = useState(user?.username || '')
  const [bio, setBio] = useState(user?.bio || '')
  const [showMap, setShowMap] = useState(false)

  const handleContinue = () => {
    login(email)
  }

  const handleLogout = () => {
    logout()
    setShowMap(false)
  }

  const handleProfileUpdate = () => {
    updateProfile({ username, bio })
  }

  const handleGoogleContinue = () => {
    const clientId = 'YOUR_GOOGLE_CLIENT_ID'
    const redirectUri = 'http://localhost:8000/oauth2callback'
    const scope = encodeURIComponent('openid email profile')
    const responseType = 'token'
    const state = 'state_parameter_passthrough_value'
    const prompt = 'select_account'

    const oauth2Url =
      'https://accounts.google.com/o/oauth2/v2/auth?client_id=' +
      clientId +
      '&redirect_uri=' +
      redirectUri +
      '&response_type=' +
      responseType +
      '&scope=' +
      scope +
      '&state=' +
      state +
      '&prompt=' +
      prompt

    window.location.href = oauth2Url
  }

  if (!user) {
    return (
      <LayoutContent showFooter={false}>
        <main className="min-h-screen flex items-center justify-center px-4">
          <div className="backdrop-blur-xl bg-white/90 rounded-2xl shadow-[0_8px_30px_rgb(0,0,0,0.12)] w-full max-w-md p-10 flex flex-col items-center transition-all duration-300 hover:shadow-[0_8px_30px_rgb(0,0,0,0.16)] hover:bg-white/95">
            <div className="text-center mb-10">
              <div className="relative w-24 h-24 mb-6 mx-auto">
                <div className="absolute inset-0 bg-gradient-to-br from-orange-400 to-orange-600 rounded-full opacity-20 blur-xl animate-pulse"></div>
                <img
                  src="https://res.cloudinary.com/glide/image/fetch/f_auto,w_150,h_150,c_lfill/https%3A%2F%2Ffirebasestorage.googleapis.com%2Fv0%2Fb%2Fglide-prod.appspot.com%2Fo%2Ficon-images%252Fanonymous-128f4fcc-612e-4486-8ccf-cdac92b60f80.png%3Falt%3Dmedia%26token%3D2aec8ef4-79e0-4e39-9117-ad32d23447a2"
                  alt="Logo"
                  className="relative w-24 h-24 transform transition-transform duration-700 hover:scale-110"
                />
              </div>
              <h1 className="text-4xl font-extrabold mb-3 bg-gradient-to-r from-orange-600 to-orange-500 bg-clip-text text-transparent tracking-tight">Welkom</h1>
              <p className="text-base text-gray-600/90 max-w-xs mx-auto">Please enter your email address to continue</p>
            </div>

            <div className="space-y-6 w-full">
              <div>
                <input
                  type="email"
                  placeholder="Enter your email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  className="w-full border border-gray-200 rounded-xl px-5 py-4 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent transition-all duration-200 bg-white/50 hover:bg-white shadow-sm text-gray-700 placeholder:text-gray-500/60"
                  aria-label="Email address"
                />
              </div>

              <button
                onClick={handleContinue}
                disabled={!email}
                className={`w-full py-4 rounded-xl font-semibold text-white ${
                  email
                    ? 'bg-gradient-to-r from-orange-500 to-orange-600 hover:from-orange-600 hover:to-orange-700 shadow-lg hover:shadow-orange-500/25 hover:-translate-y-0.5'
                    : 'bg-orange-400/50 cursor-not-allowed'
                } transition-all duration-300`}
              >
                Continue
              </button>

              <div className="relative my-8">
                <div className="absolute inset-0 flex items-center">
                  <div className="w-full border-t border-gray-200/60"></div>
                </div>
                <div className="relative flex justify-center text-sm">
                  <span className="px-6 py-1 backdrop-blur-xl bg-white/90 text-gray-500 rounded-full border border-gray-200/60">
                    Or continue with
                  </span>
                </div>
              </div>

              <button
                onClick={handleGoogleContinue}
                className="w-full py-4 rounded-xl font-semibold bg-white border border-gray-200 flex items-center justify-center gap-3 hover:bg-white transition-all duration-300 shadow-sm hover:shadow-md hover:-translate-y-0.5 group"
              >
                <img
                  src="https://www.gstatic.com/firebasejs/ui/2.0.0/images/auth/google.svg"
                  alt="Google logo"
                  className="w-5 h-5 transition-transform group-hover:scale-110"
                />
                <span className="text-gray-700 group-hover:text-gray-900">Continue with Google</span>
              </button>
            </div>
          </div>
        </main>
      </LayoutContent>
    )
  }

  return (
    <LayoutContent>
      <main className="min-h-screen flex flex-col items-start justify-start px-4 pb-20 w-full">
        <Navigation activeTab={activeTab} onTabChange={setActiveTab} />
        <div className="max-w-7xl w-full mx-auto grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          <div className="backdrop-blur-xl bg-white/90 rounded-2xl shadow-[0_8px_30px_rgb(0,0,0,0.12)] w-full p-8 flex flex-col items-center transition-all duration-300 hover:shadow-[0_8px_30px_rgb(0,0,0,0.16)] hover:bg-white/95">
            <h1 className="text-3xl font-extrabold mb-6 text-gray-900 tracking-tight">Welcome, {user.username}</h1>
            <div className="mb-6 p-4 bg-white rounded-xl shadow-inner">
              <QRCodeCanvas value={user.qrCodeData || ''} size={128} className="rounded" />
            </div>
            <input
              type="text"
              placeholder="Username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              className="w-full border border-gray-200 rounded-xl px-4 py-3 mb-6 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent transition-all duration-200 bg-white/50 hover:bg-white"
              aria-label="Username"
            />
            <textarea
              placeholder="Short bio"
              value={bio}
              onChange={(e) => setBio(e.target.value)}
              className="w-full border border-gray-200 rounded-xl px-4 py-3 mb-6 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent transition-all duration-200 bg-white/50 hover:bg-white resize-none"
              rows={3}
              aria-label="Bio"
            />
            <button
              onClick={handleProfileUpdate}
              className="w-full py-3 rounded-xl font-semibold text-white bg-gradient-to-r from-orange-500 to-orange-600 hover:from-orange-600 hover:to-orange-700 shadow-lg hover:shadow-orange-500/25 transition-all duration-300 transform hover:scale-[1.02] mb-6"
            >
              Update Profile
            </button>
            <button
              onClick={() => setShowMap(!showMap)}
              className="w-full py-3 rounded-xl font-semibold text-white bg-gradient-to-r from-orange-600 to-orange-700 hover:from-orange-700 hover:to-orange-800 shadow-lg hover:shadow-orange-600/25 transition-all duration-300 transform hover:scale-[1.02] mb-6"
            >
              {showMap ? 'Hide Map' : 'Show Map'}
            </button>
            <button
              onClick={handleLogout}
              className="w-full py-3 rounded-xl font-semibold text-orange-600 border-2 border-orange-600/30 hover:bg-orange-600 hover:text-white transition-all duration-300 transform hover:scale-[1.02] hover:border-orange-600"
            >
              Logout
            </button>
          </div>

          {showMap && (
            <div className="col-span-1 md:col-span-2 lg:col-span-3 w-full">
              <Map />
            </div>
          )}

          <div className="backdrop-blur-xl bg-white/90 rounded-2xl shadow-[0_8px_30px_rgb(0,0,0,0.12)] p-8 transition-all duration-300 hover:shadow-[0_8px_30px_rgb(0,0,0,0.16)] hover:bg-white/95">
            <EventManager />
          </div>

          <div className="backdrop-blur-xl bg-white/90 rounded-2xl shadow-[0_8px_30px_rgb(0,0,0,0.12)] p-8 transition-all duration-300 hover:shadow-[0_8px_30px_rgb(0,0,0,0.16)] hover:bg-white/95">
            <ClubManager />
          </div>

          <div className="backdrop-blur-xl bg-white/90 rounded-2xl shadow-[0_8px_30px_rgb(0,0,0,0.12)] p-8 transition-all duration-300 hover:shadow-[0_8px_30px_rgb(0,0,0,0.16)] hover:bg-white/95">
            <FriendSystem />
          </div>

          <div className="backdrop-blur-xl bg-white/90 rounded-2xl shadow-[0_8px_30px_rgb(0,0,0,0.12)] p-8 transition-all duration-300 hover:shadow-[0_8px_30px_rgb(0,0,0,0.16)] hover:bg-white/95">
            <AdminDashboard />
          </div>

          <div className="backdrop-blur-xl bg-white/90 rounded-2xl shadow-[0_8px_30px_rgb(0,0,0,0.12)] p-8 transition-all duration-300 hover:shadow-[0_8px_30px_rgb(0,0,0,0.16)] hover:bg-white/95">
            <Monetization />
          </div>
        </div>
      </main>
    </LayoutContent>
  )
}
