'use client'

import { useState } from 'react'

interface Event {
  id: string
  title: string
  description: string
  imageUrl?: string
  date: string
  startTime: string
  endTime: string
  location: string
  type: 'public' | 'private' | 'invite-only'
  tags: string[]
  guestLimit?: number
}

export default function EventManager() {
  const [events, setEvents] = useState<Event[]>([])
  const [form, setForm] = useState<Omit<Event, 'id'>>({
    title: '',
    description: '',
    imageUrl: '',
    date: '',
    startTime: '',
    endTime: '',
    location: '',
    type: 'public',
    tags: [],
    guestLimit: undefined,
  })

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target
    setForm((prev) => ({
      ...prev,
      [name]: name === 'guestLimit' ? (value ? parseInt(value) : undefined) : value,
    }))
  }

  const handleTagsChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const tags = e.target.value.split(',').map((tag) => tag.trim())
    setForm((prev) => ({ ...prev, tags }))
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    const newEvent: Event = {
      id: Math.random().toString(36).substring(2, 10),
      ...form,
    }
    setEvents((prev) => [...prev, newEvent])
    setForm({
      title: '',
      description: '',
      imageUrl: '',
      date: '',
      startTime: '',
      endTime: '',
      location: '',
      type: 'public',
      tags: [],
      guestLimit: undefined,
    })
  }

  return (
    <div className="max-w-3xl mx-auto p-4 bg-white rounded-lg shadow-lg">
      <h2 className="text-xl font-bold mb-4">Create / Manage Events</h2>
      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label htmlFor="title" className="block font-semibold mb-1">
            Title
          </label>
          <input
            type="text"
            id="title"
            name="title"
            value={form.title}
            onChange={handleChange}
            required
            className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
          />
        </div>
        <div>
          <label htmlFor="description" className="block font-semibold mb-1">
            Description
          </label>
          <textarea
            id="description"
            name="description"
            value={form.description}
            onChange={handleChange}
            required
            rows={3}
            className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
          />
        </div>
        <div>
          <label htmlFor="imageUrl" className="block font-semibold mb-1">
            Image URL / Flyer
          </label>
          <input
            type="url"
            id="imageUrl"
            name="imageUrl"
            value={form.imageUrl}
            onChange={handleChange}
            className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
          />
        </div>
        <div className="grid grid-cols-3 gap-4">
          <div>
            <label htmlFor="date" className="block font-semibold mb-1">
              Date
            </label>
            <input
              type="date"
              id="date"
              name="date"
              value={form.date}
              onChange={handleChange}
              required
              className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
            />
          </div>
          <div>
            <label htmlFor="startTime" className="block font-semibold mb-1">
              Start Time
            </label>
            <input
              type="time"
              id="startTime"
              name="startTime"
              value={form.startTime}
              onChange={handleChange}
              required
              className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
            />
          </div>
          <div>
            <label htmlFor="endTime" className="block font-semibold mb-1">
              End Time
            </label>
            <input
              type="time"
              id="endTime"
              name="endTime"
              value={form.endTime}
              onChange={handleChange}
              required
              className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
            />
          </div>
        </div>
        <div>
          <label htmlFor="location" className="block font-semibold mb-1">
            Location (address or description)
          </label>
          <input
            type="text"
            id="location"
            name="location"
            value={form.location}
            onChange={handleChange}
            required
            className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
          />
        </div>
        <div>
          <label htmlFor="type" className="block font-semibold mb-1">
            Type
          </label>
          <select
            id="type"
            name="type"
            value={form.type}
            onChange={handleChange}
            className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
          >
            <option value="public">Public</option>
            <option value="private">Private</option>
            <option value="invite-only">Invite Only</option>
          </select>
        </div>
        <div>
          <label htmlFor="tags" className="block font-semibold mb-1">
            Tags (comma separated)
          </label>
          <input
            type="text"
            id="tags"
            name="tags"
            value={form.tags.join(', ')}
            onChange={handleTagsChange}
            className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
          />
        </div>
        <div>
          <label htmlFor="guestLimit" className="block font-semibold mb-1">
            Guest Limit (optional)
          </label>
          <input
            type="number"
            id="guestLimit"
            name="guestLimit"
            value={form.guestLimit ?? ''}
            onChange={handleChange}
            min={1}
            className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
          />
        </div>
        <button
          type="submit"
          className="w-full py-2 rounded-md font-semibold text-white bg-gradient-to-r from-[#f4a58a] to-[#d97a5a] hover:from-[#d97a5a] hover:to-[#f4a58a] transition-colors duration-300"
        >
          Create Event
        </button>
      </form>

      <div className="mt-8">
        <h3 className="text-lg font-semibold mb-2">Events List</h3>
        {events.length === 0 ? (
          <p>No events created yet.</p>
        ) : (
          <ul className="space-y-4">
            {events.map((event) => (
              <li key={event.id} className="border border-gray-300 rounded-md p-4">
                <h4 className="font-bold">{event.title}</h4>
                <p>{event.description}</p>
                <p>
                  Date: {event.date} | {event.startTime} - {event.endTime}
                </p>
                <p>Location: {event.location}</p>
                <p>Type: {event.type}</p>
                <p>Tags: {event.tags.join(', ')}</p>
                {event.guestLimit && <p>Guest Limit: {event.guestLimit}</p>}
                {event.imageUrl && (
                  <img src={event.imageUrl} alt={event.title} className="mt-2 max-h-40 object-cover rounded" />
                )}
              </li>
            ))}
          </ul>
        )}
      </div>
    </div>
  )
}
