'use client'

import { useState } from 'react'

interface Report {
  id: string
  type: 'user' | 'content'
  description: string
  status: 'open' | 'resolved'
}

interface Metric {
  name: string
  value: number
}

export default function AdminDashboard() {
  const [reports, setReports] = useState<Report[]>([
    { id: '1', type: 'user', description: 'Inappropriate behavior reported', status: 'open' },
    { id: '2', type: 'content', description: 'Spam event reported', status: 'resolved' },
  ])

  const [metrics, setMetrics] = useState<Metric[]>([
    { name: 'Active Users', value: 1200 },
    { name: 'Events Created', value: 350 },
    { name: 'Clubs Registered', value: 45 },
  ])

  const resolveReport = (id: string) => {
    setReports((prev) =>
      prev.map((report) => (report.id === id ? { ...report, status: 'resolved' } : report))
    )
  }

  return (
    <div className="max-w-4xl mx-auto p-6 bg-white rounded-lg shadow-lg mt-8">
      <h2 className="text-2xl font-bold mb-6">Admin Dashboard</h2>

      <section className="mb-8">
        <h3 className="text-xl font-semibold mb-4">Metrics</h3>
        <div className="grid grid-cols-3 gap-4">
          {metrics.map((metric) => (
            <div key={metric.name} className="p-4 bg-gray-100 rounded">
              <p className="text-lg font-bold">{metric.value}</p>
              <p className="text-gray-600">{metric.name}</p>
            </div>
          ))}
        </div>
      </section>

      <section>
        <h3 className="text-xl font-semibold mb-4">Reports</h3>
        {reports.length === 0 ? (
          <p>No reports available.</p>
        ) : (
          <ul className="space-y-4">
            {reports.map((report) => (
              <li
                key={report.id}
                className={`p-4 rounded border ${
                  report.status === 'open' ? 'border-red-500 bg-red-50' : 'border-green-500 bg-green-50'
                }`}
              >
                <p>
                  <strong>Type:</strong> {report.type}
                </p>
                <p>
                  <strong>Description:</strong> {report.description}
                </p>
                <p>
                  <strong>Status:</strong> {report.status}
                </p>
                {report.status === 'open' && (
                  <button
                    onClick={() => resolveReport(report.id)}
                    className="mt-2 px-3 py-1 bg-green-500 text-white rounded hover:bg-green-600"
                  >
                    Mark as Resolved
                  </button>
                )}
              </li>
            ))}
          </ul>
        )}
      </section>
    </div>
  )
}
