'use client'

import { useState } from 'react'

interface Club {
  id: string
  name: string
  description: string
  openingHours: string
  tags: string[]
  linkedEvents: string[]
}

export default function ClubManager() {
  const [clubs, setClubs] = useState<Club[]>([])
  const [form, setForm] = useState<Omit<Club, 'id'>>({
    name: '',
    description: '',
    openingHours: '',
    tags: [],
    linkedEvents: [],
  })

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target
    setForm((prev) => ({
      ...prev,
      [name]: value,
    }))
  }

  const handleTagsChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const tags = e.target.value.split(',').map((tag) => tag.trim())
    setForm((prev) => ({ ...prev, tags }))
  }

  const handleLinkedEventsChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const linkedEvents = e.target.value.split(',').map((id) => id.trim())
    setForm((prev) => ({ ...prev, linkedEvents }))
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    const newClub: Club = {
      id: Math.random().toString(36).substring(2, 10),
      ...form,
    }
    setClubs((prev) => [...prev, newClub])
    setForm({
      name: '',
      description: '',
      openingHours: '',
      tags: [],
      linkedEvents: [],
    })
  }

  return (
    <div className="max-w-3xl mx-auto p-4 bg-white rounded-lg shadow-lg mt-8">
      <h2 className="text-xl font-bold mb-4">Add / Manage Clubs</h2>
      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label htmlFor="name" className="block font-semibold mb-1">
            Club Name
          </label>
          <input
            type="text"
            id="name"
            name="name"
            value={form.name}
            onChange={handleChange}
            required
            className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
          />
        </div>
        <div>
          <label htmlFor="description" className="block font-semibold mb-1">
            Description
          </label>
          <textarea
            id="description"
            name="description"
            value={form.description}
            onChange={handleChange}
            required
            rows={3}
            className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
          />
        </div>
        <div>
          <label htmlFor="openingHours" className="block font-semibold mb-1">
            Opening Hours
          </label>
          <input
            type="text"
            id="openingHours"
            name="openingHours"
            value={form.openingHours}
            onChange={handleChange}
            placeholder="e.g. Mon-Fri 9am - 2am"
            className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
          />
        </div>
        <div>
          <label htmlFor="tags" className="block font-semibold mb-1">
            Tags (comma separated)
          </label>
          <input
            type="text"
            id="tags"
            name="tags"
            value={form.tags.join(', ')}
            onChange={handleTagsChange}
            className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
          />
        </div>
        <div>
          <label htmlFor="linkedEvents" className="block font-semibold mb-1">
            Linked Events (event IDs, comma separated)
          </label>
          <input
            type="text"
            id="linkedEvents"
            name="linkedEvents"
            value={form.linkedEvents.join(', ')}
            onChange={handleLinkedEventsChange}
            className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
          />
        </div>
        <button
          type="submit"
          className="w-full py-2 rounded-md font-semibold text-white bg-gradient-to-r from-[#f4a58a] to-[#d97a5a] hover:from-[#d97a5a] hover:to-[#f4a58a] transition-colors duration-300"
        >
          Add Club
        </button>
      </form>

      <div className="mt-8">
        <h3 className="text-lg font-semibold mb-2">Clubs List</h3>
        {clubs.length === 0 ? (
          <p>No clubs added yet.</p>
        ) : (
          <ul className="space-y-4">
            {clubs.map((club) => (
              <li key={club.id} className="border border-gray-300 rounded-md p-4">
                <h4 className="font-bold">{club.name}</h4>
                <p>{club.description}</p>
                <p>Opening Hours: {club.openingHours}</p>
                <p>Tags: {club.tags.join(', ')}</p>
                <p>Linked Events: {club.linkedEvents.join(', ')}</p>
              </li>
            ))}
          </ul>
        )}
      </div>
    </div>
  )
}
